.Loader {
     width: 30%;
     height: 80vh;
     display: flex;
     flex-direction: row;
     justify-content: space-around;
     align-items: center;
}



.circle {
     width: 4rem;
     height: 4rem;
     border-radius: 50%;
     position: relative;
}

.circle:nth-child(1) {
     background-color: #90be6d;
}

.circle:nth-child(2) {
     background-color: #f9c74f;
}

.circle:nth-child(3) {
     background-color: #f8961e;
}

.circle:nth-child(4) {
     background-color: #f3722c;
}

.circle:nth-child(5) {
     background-color: #f94346;
}

.circle::before {
     content: "";
     width: 100%;
     height: 100%;
     position: absolute;
     border-radius: 50%;
     opacity: 0.5;
     -webkit-animation: animateLoader3 2s ease-out infinite;
             animation: animateLoader3 2s ease-out infinite;
}

.circle:nth-child(1)::before {
     background-color: #90be6d;
}

.circle:nth-child(2)::before {
     background-color: #f9c74f;
     -webkit-animation-delay: 0.2s;
             animation-delay: 0.2s;
}

.circle:nth-child(3)::before {
     background-color: #f8961e;
     -webkit-animation-delay: 0.4s;
             animation-delay: 0.4s;
}

.circle:nth-child(4)::before {
     background-color: #f3722c;
     -webkit-animation-delay: 0.6s;
             animation-delay: 0.6s;
}

.circle:nth-child(5)::before {
     background-color: #f94346;
     -webkit-animation-delay: 0.8s;
             animation-delay: 0.8s;
}

@-webkit-keyframes animateLoader3 {
     0% {
          -webkit-transform: scale(1);
                  transform: scale(1);
     }

     50%,
     75% {
          -webkit-transform: scale(2.5);
                  transform: scale(2.5);
     }

     80%,
     100% {
          opacity: 0;
     }
}

@keyframes animateLoader3 {
     0% {
          -webkit-transform: scale(1);
                  transform: scale(1);
     }

     50%,
     75% {
          -webkit-transform: scale(2.5);
                  transform: scale(2.5);
     }

     80%,
     100% {
          opacity: 0;
     }
}

@media (max-width:700px) {
     .Loader {
          width: 80%;
     }
}
